.video {
   padding:0 20px 60px;
   &-block {
      width:auto;
      background-color: #D9D9D9;
      min-height: 280px;
      margin-left: -20px;
      margin-right: -20px;
      border-radius: 15px;
   }
   &-buttons {
      display: flex;

      flex-direction: column;
      gap:20px;
      @media (min-width: 992px) {
         flex-direction: row;
         justify-content: center;
         gap: 50px
       }
   }
}
